import React, { useEffect, useState } from 'react';
import styled from "styled-components";
import NavItem, { NavItemProps } from './NavItem';
import List from './List';
import { faPlus as PlusIcon } from '@fortawesome/pro-solid-svg-icons/faPlus';
import { faMinus as MinusIcon } from '@fortawesome/pro-solid-svg-icons/faMinus';
import ClickableIcon from './ClickableIcon';

type NavItemConfig = {
  icon?: React.ReactNode,
  depth?: number,
  path: string,
  title: string,
  children?: NavItemConfig[],
}

export type NavItemsConfig = Array<NavItemConfig>

export type NavItemsProps = {
  show?: boolean,
  items: NavItemsConfig,
  parent?: NavItemConfig,
  depth: number
}

const Container = styled(List)
.withConfig({
  shouldForwardProp: (prop, defaultFn) => 
    !['show'].includes(prop) && defaultFn(prop)
})`
  &.root {
    margin-left: 0;
  }
  ${(p: {show?: boolean, className?: string}) => `
    display: ${p.show && 'block' || 'none'};
  `}
`

const RightIcon = styled(ClickableIcon)`
  color: #FF6820;
  width: 0.9375rem;
  height: 0.9375rem;
  display: inline-block;
`;

const ExpandedIcon = styled(RightIcon)`
  color: #FF6820;
`
const CollapseIcon = styled(RightIcon)`
  color: #00AFD9;
`

export const NavItems = ({ items, depth, show}: NavItemsProps) => {
  const [active, setActive] = useState<number>();
  const [activeItem, setActiveItem]= useState<any>();
  let mounted = false;
  const handleGetProps = ({location, ...props}: any, i: number) => {
    if (
      mounted && 
      (props.isCurrent && !activeItem 
      || props.isCurrent && activeItem.href !== props.href)
    ) {
      setActiveItem({...props, i});
    }
    return {
      'data-active': props.isCurrent
    };
  };

  useEffect(() => {
    mounted = true;
    if (activeItem && active !== activeItem.i) {
      setActive(activeItem.i);
    }
  }, [activeItem]);

  const toogleShow = (i: number) => 
    setActive(active === i ? undefined : i);

  const renderItem = (item: NavItemConfig, i: number) => {
    const hasChildren = item.children && item.children?.length > 0;
    const rightIcon = hasChildren && (
      active === i ? 
      <ExpandedIcon
        onClick={() => toogleShow(i)} icon={MinusIcon} />
      :
      <CollapseIcon
        onClick={() => toogleShow(i)} icon={PlusIcon}/>
    )
    return (
      <React.Fragment key={'nav'+i}>
        <NavItem
          {...item}
          isChild={depth > 0}
          getProps={(detail: any) => handleGetProps(detail, i)}
          rightIcon={rightIcon}
        >
          <NavItems
            depth={depth + 1}
            items={item.children || []}
            parent={item}
            show={active === i}
          />
        </NavItem>
      </React.Fragment>
    )
  }
  if (items.length === 0) {
    return (null);
  }

  return (
    <Container
      show={show || depth == 0}
      className={depth === 0 ? 'root' : ''}
    >
      {items.map(renderItem)}
    </Container>
  )
}

export default NavItems;