import React from 'react';
import { media } from 'styled-bootstrap-grid';
import styled from "styled-components";
import Link, {LinkProps} from './Link';
import ListItem from "./ListItem";

const Container = styled(ListItem)`
  margin-bottom: 0;
  &.root {
    padding: 6px 0;
    > a {
      padding-left: 1.5625rem;
      &[data-active="true"] {
        background: #F3F3F3;
        border-radius: 0px 10px 10px 0px;
      }
    }
    ${media.xs`
      padding: 0;
      border-bottom: 1px solid #B7D4E54D;

      > a {
        height: 72px;
        padding-right: 16.5px;
        padding-left: 16.5px;
        border-radius: 0 !important;
      }
    `}
  }
  
  &.child {
    margin-left: 25px;
    margin-right: 0.875rem;
    border-bottom: 1px solid #B7D4E54D;
    > a[data-active="true"] {
      color: #00AFD9;
    }
    &:last-child {
      border-bottom: none;
    }

    ${media.xs`
      margin-left: 22px;
    `}
  }
`

const ContainerItem = styled.div`
  display: flex;
  flex: 1;
  // justify-content: space-between;
`

const Icon = styled.div`
  margin-right: 13px;
`

const RightIcon = styled.div`
  margin-right: 0.875rem;
  ${media.xs`
    margin-right: 0;
  `}
`

const ItemTitle = styled.div`
  font-size: 1.125rem;
`

const LinkItem = styled(Link)`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 2.8125rem;
  font-weight: 700;
  align-items: center;
  &[data-active="true"] {
    .left-icon {
      color: #FF6820;
    }
  }
`

export type NavItemProps = {
  icon?: React.ReactNode,
  depth?: number,
  path: string,
  title: string,
  children?: React.ReactNode,
  getProps?: LinkProps['getProps'],
  rightIcon?: React.ReactNode,
  isChild?: boolean,
}


export const NavItem = ({ icon, title, path, rightIcon, getProps, isChild, children}: NavItemProps) => {
  return (
    <>
      <Container
        className={isChild ? 'child' : 'root'}
      >
        <LinkItem to={path} getProps={getProps}>
          <ContainerItem>
            {icon && <Icon className="left-icon">{icon}</Icon>}
            <ItemTitle>{title}</ItemTitle>
          </ContainerItem>
          {rightIcon && <RightIcon>{rightIcon}</RightIcon>}
        </LinkItem>
        {children}
      </Container>
    </>
  )
}

export default NavItem;