/*
 * Layout component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/use-static-query/
 */

import React, { useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
import PropTypes from 'prop-types';
import { useStaticQuery, graphql } from 'gatsby';
import Header from './Header';
import './layout.css';
import { todosRequested } from '@app/store/actions/todos';
import NavBar from './Navbar';
import styled from 'styled-components';
import { useTheme } from '@app/ui/theme/Context';
import { Container as BaseContainer, media } from 'styled-bootstrap-grid';

type Props = {
  navbarWidth?: string;
}

const Body = styled('div')
.withConfig<Props>({
  shouldForwardProp: (prop, defaultFn) => !['width'].includes(prop) && defaultFn(prop)
})`
  ${media.phone(`
    padding-left: 0;
  `)}
  ${media.desktop`
    padding-left: ${(p: any) => p.theme.navbar.width};
  `}
`

const Container = styled(BaseContainer)`
  background-color: #ECF3FA;
  ${media.max.desktop`
    padding-top: 15px;
  `}
  ${media.desktop`
    padding-left: 2.5rem;
    padding-right: 2.5rem;
    padding-top: 2.625rem;
  `}
`

const isMobile = typeof window !== 'undefined' && window.innerWidth < 576;

const Layout = ({ children }: { children: any }) => {
  const theme = useTheme();
  const [isNavOpen, setNavOpen] = useState(!isMobile)
  const dispatch = useDispatch();

  useEffect(() => {
    if (typeof window !== 'undefined') {
      window.onresize = () => {
        const isMobile = window.innerWidth < 992;
        setNavOpen(!isMobile);
      }
    }


    /*
     * This is an example of doing things when the app first loads.
     * You can dispatch a Redux action here to do some async thing
     * when the webapp boots up.
     */

    dispatch(todosRequested());
  }, []);

  const data = useStaticQuery(graphql`
    query SiteTitleQuery {
      site {
        siteMetadata {
          title
        }
      }
    }
  `);

  return (
    <>
      <Header
        title={data.site.siteMetadata.title}
        onOpenMenu={() => setNavOpen(!isNavOpen)}
      />
      <NavBar 
        open={isNavOpen}
        onClose={() => setNavOpen(false)}
      />
      <Body
        // navbarWidth={theme.navbar.width}
      >
        <Container as="main" fluid>
          {children}
        </Container>
      </Body>
    </>
  );
};

Layout.propTypes = {
  children: PropTypes.node.isRequired,
};

export default Layout;
