import React from 'react';
import styled from 'styled-components';
import FontIcon, {FontIconProps} from './FontIcon';

export type ClickableIconProps = FontIconProps & {

}

const Container = styled.div`
  display: inline-block;
  cursor: pointer;
`

export const ClickableIcon = ({ onClick, ...rest }: ClickableIconProps) => (
  <Container
    onClick={(e: any) => {
      e.preventDefault();
      onClick && onClick(e);
    }}
  >
    <FontIcon
      {...rest}
    />
  </Container>
)

export default ClickableIcon;