import React from 'react'
import styled from 'styled-components'
import Link from 'gatsby-link';
import { media } from 'styled-bootstrap-grid';
import { logo } from "@app/assets/images";
import ClickableIcon from '@app/ui/components/ClickableIcon';
import {faBars as IconMenu} from '@fortawesome/pro-solid-svg-icons/faBars';

export type Props = {
  logo?: string,
  navitems?: Array<{
    title?: string,
    url?: boolean | string,
    children?: boolean | Array<{
      title?: string,
      url?: string,
    }>
  }>,
  title?: string,
  onOpenMenu: () => void
}
const Container = styled.div`
  width: 100%;
  height: ${p => p.theme.header.height};
  background: ${p => p.theme.header.backgroundColor} 0% 0% no-repeat padding-box;
  opacity: 1; 
  align-items: center;
  display:flex;
  justify-content: space-between;
`

const LogoWrapper = styled.div`
  
`

const Logo = styled.img`
  width: 5.8125rem;
  height: 2.25rem;
  cursor: pointer;
  margin-bottom: 0;	
  ${media.xl`
    width: 6.81125rem;
    height: 2.605rem;
  `}
`

const MenuToogle = styled(ClickableIcon)`
  font-size: 24px;
  color: ${p => p.theme.colors.white};
`

export const Header: React.FC<Props> = ({
  title, onOpenMenu
}) => {
  return(
    <Container>
      <LogoWrapper>
        <Logo
          src={logo}
          alt="logo"
        />
      </LogoWrapper>
      <MenuToogle
        icon={IconMenu}
        onClick={onOpenMenu}
      />
    </Container>
  )
}

export default Header
